import React from "react"
import Avatar from '../components/avatar'
import Info from '../components/info'

export default () => (
    <div style={{
        display: `flex`,
        flexDirection: `column`,
        margin: `20px`,
    }}>
        <div style={{
            display: `flex`,
            flex: 1
        }}>
            <Avatar>
                <span style={{
                    position: `relative`,
                    top: `25%`,
                    left: `25%`,
                    color: `#fff`,
                }}>
                    SF
                </span>
            </Avatar>
        </div>
        <div style={{
            flex: 1
        }}>
            <Info/>
        </div>
    </div>
)

